.navbar{
    position: fixed;
    top: 0;
    height: 50px; 
    width: 100%;
    display: flex;
    justify-content: space-between;
    background-color: #111;
}



.logo{
    position: fixed;
    left: 20px;
    width: 80px;
    top: 14px;
}
.avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    top: 14px;
}
*{
    margin: 0;
    padding: 0;
}
body{
    font-family: 'Courier New', Courier, monospace;
    background-color: #111;

}
.banner{
    
    background-size: cover;
    height: 448px;
    width: 100%;
    color: white;
    
}
.content{
    
    padding-top: 140px;
    height: 190px;
    padding-left: 15px;

}
.title{
    
    
    font-size: 3rem;
    font-size: 800;
    padding-bottom: 0.3rem;
}
.button{
    left: 20px;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51,51,51,00.5);
    cursor: pointer;
    margin-right: 1rem;
}
.button:hover{
    color: black;
    background-color:#e6e6e6;
}
.description{
    line-height: 1.3rem;
    width: 45rem;
    padding-top: 1rem;
    font-size: 1.2rem;
    height: 80px;
    max-width: 500px;
    
}
.fade_buttom{
    height: 8rem;
    background-image: linear-gradient(180deg,transparent, rgba(37,37,37,0.61),#111 );
}


 .row{
    margin-left: 20px;
    color:#ffff;


 }
 .posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;

 }
 .posters::-webkit-scrollbar{
    display: none;
 }
 .poster{
    max-height: 250px; 
    margin-right: 10px;
    
 }
 .smallposter{
   max-height: 150px;
   margin-right: 10px;
   
}
