.banner{
    
    background-size: cover;
    height: 448px;
    width: 100%;
    color: white;
    
}
.content{
    
    padding-top: 140px;
    height: 190px;
    padding-left: 15px;

}
.title{
    
    
    font-size: 3rem;
    font-size: 800;
    padding-bottom: 0.3rem;
}
.button{
    left: 20px;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51,51,51,00.5);
    cursor: pointer;
    margin-right: 1rem;
}
.button:hover{
    color: black;
    background-color:#e6e6e6;
}
.description{
    line-height: 1.3rem;
    width: 45rem;
    padding-top: 1rem;
    font-size: 1.2rem;
    height: 80px;
    max-width: 500px;
    
}
.fade_buttom{
    height: 8rem;
    background-image: linear-gradient(180deg,transparent, rgba(37,37,37,0.61),#111 );
}

